<template>

<div class="warp">
    <navbar></navbar> 
    <div class="show_page">
    <transition name="slide-fade" mode='in-out'>
        <router-view>
        </router-view>    
    </transition>
    </div>
    </div>

</template>

<style>
.warp{
    overflow:hidden;
    height:100%;
    color:white;
}
.slide-fade-enter-active {
    position:absolute;
    transition: all 1s cubic-bezier(1.0, 0.5, 0.8, 1.0); 
    }
.slide-fade-leave-active {
    position:absolute;
    transform: translateX(-5%);
    transition: all 1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
    opacity: 0;
    }
.slide-fade-enter{
    position:absolute;
    transform: translateX(5%);
    opacity: 0;
    }
.show_page{
    position:absolute;
    right:10%;
    bottom:0;
    width:60%;
    top:20%;
}
</style>
<script>
    import Navbar from '../components/Navbar'
    export default{
        name:"home",
        components:{Navbar}
    }
</script>


