<template>
    <div class="hello">
       <router-link to="/home">
        <img src="../../static/img/head_pic.jpg" alt="head_picture">
        </router-link>
         <p>高莹</p> 
    </div>
</template>
<style>
.hello{
    width:100%;
    background-color:#843534;
    font-size:1.2rem;
    height:100%;
    font-family:Helvetica, Arial, sans-serif;
    position:absolute;
}
.hello a{
    display:block;
    width:2rem;
    height:2rem;
    border-radius:50%;
    text-decoration:none;
    transition:all 0.5s ease;
    text-align:center;
    overflow:hidden;
    position:relative;
    top:33%;
    box-shadow:0 0 10px 5px rgba(0,0,0,.2);
    transition:all 1s ease;
    margin:0 auto;
}
.hello a:hover{
    box-shadow:0 0 10px 8px rgba(0,0,0,.25);
}
a img{
    display:block;
    width:100%;
    height:100%;
}
.hello p{
    text-align:center;
    color:#fff;
    font-size:0.4rem;
    position:relative;
    top:35%;
    width:20%;
    margin:0 auto;
    letter-spacing:0.2rem;
    padding-left:0.2rem;
} 
</style>
<script>
    export default{
        name:"hello",
    }
</script>


