<template>
      <div class="nav-vertical-left">
        <div class="head_pic">
        <img src="../../static/img/head_pic.jpg" alt="head_picture">
        <p>高莹</p>
        <span><i class="fa fa-phone">18108224071</i></span>
        </div>
        <ul>
        <li  v-bind:class="{'check':index==currentIndex}" @click="currentIndex=index" v-for="(item,index) in items" :key="item">
        <router-link :to="item.link" ><i :class="item.class">{{item.name}} </i>   
        </router-link></li>
        </ul>
      </div>
</template>
<style scoped>
.nav-vertical-left{
  height:100%;
  position:absolute;
  bottom:0;
  top:0;
  width:20%;
  color:#fff;
  background-color:#843534;
  text-align:center;
}
.head_pic i{
  margin:2%;
}
.head_pic span{
  font-size:0.25rem;
}
.head_pic{
  font-size:0.4rem;
}
.head_pic img{
  display:block;
  width:60%;
  height:60%;
  border-radius:50%;
  margin-left:20%;
  margin-top:20%;
}
.head_pic p{
  margin-top:0.2rem;
  letter-spacing:0.2rem;
  padding-left:0.2rem;
}
.nav-vertical-left ul{
  list-style-type:none;
  padding:5% 0;
  font-size:0.2rem;
  margin-top:10%;
}
.nav-vertical-left ul li{
  line-height:0.8rem;
  height:20%;
  transition:all 1s ease;
}
.nav-vertical-left ul li a{
  display:block;
  text-decoration:none;
  color:#fff;
  letter-spacing:0.05rem;
}
.nav-vertical-left li:hover{
  background-color:rgba(255,255,255,0.5);
}
.check{
  background-color:rgba(255,255,255,0.5);
}

</style>
<script>
  export default{
    name:"navbar",
    data(){
      return{
        currentIndex:"",
          items:[{
            name:"基本信息",
            link:"/home",
            class:"fa fa-user"
          },
          {
            name:"专业技能",
            link:"/home/skill",
            class:"fa fa-lightbulb-o"
          },
          {
            name:"项目经验",
            link:"/home/more",
            class:"fa fa-codepen"
          },] 
      }
    },
    methods:{
      bg_transition:function(){

      }
    }
  }
</script>


